<template>
    <button 
        @click="toRoute()"
        class="mx-10 my-4 py-2 bg-gradient-to-r from-indigo-900 to-indigo-600 font-bold text-white text-xl text-center rounded-xl">
        {{text}}
    </button>
</template>

<script>
export default {
  name: 'Button',
  methods: {
    toRoute: function(){
      this.$store.dispatch('setCurrentYear',this.param.year);
      this.$store.dispatch('setCurrentTitle',this.param.title);
      this.$router.push({ name: this.name, params: this.param });
    }
  },
  props: {
    name: String,
    text: String,
    param: Object
  },
}
</script>